.app {
    /* background: linear-gradient(to bottom, white, #bae9f5); */
    background: linear-gradient(to bottom, white 35%, #cddfef 85%);
    display: grid;
    place-items: center;
    /* grid-template-columns: repeat(auto-fit, minmax(500px, 1fr)); */
    grid-template-columns: 1fr 2fr;
    padding: 10px 20px;
    box-sizing: border-box;
    height: 100vh;
}

.auth-box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    /* box-sizing: border-box; */
}
.auth-container {
    padding: 10px;
    width: 500px;
    max-width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.image-box {
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    overflow: hidden;
}

.image-box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 18px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.heading {
    color: black;
    font-size: 2rem;
    margin-bottom: 10px;
}

.sub-heading {
    color: black;
    font-size: 1.5rem;
}

