.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;}

.logo {
    max-width: 200px;
    max-height: 100%;
    height:100%;
    width:100%;
}

.link {
  text-decoration: none;
  display: inline !important;
}


/* auto fill */
.remember-me {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
    width: 90%;
    gap: 1rem;
    box-sizing: border-box;
}

@media (max-width: 1100px) {
    .auth-container {
        width: 100%;
        max-width:100vw;
        padding: 20px;
    }
    .image-box {
        display: none;
    }
    .app {
        display:flex;
        flex-direction: column;
        align-items:center;
        justify-content:center
    }
}